// Import the necessary modules
import { signInWithPopup, GoogleAuthProvider , signInWithEmailAndPassword,createUserWithEmailAndPassword} from 'firebase/auth';
import { getAuth, sendPasswordResetEmail } from 'firebase/auth';
import { initializeApp } from 'firebase/app';
import { getFirestore } from 'firebase/firestore';
import { getStorage } from 'firebase/storage';
import { getAnalytics, logEvent } from 'firebase/analytics';

// Your web app's Firebase configuration
const firebaseConfig = {

  
  apiKey: "AIzaSyA1n1y2n-tshbTus7dE5GDKk2EIo4oBtNg",
  authDomain: "invoicegeneator.firebaseapp.com",
  projectId: "invoicegeneator",
  storageBucket: "invoicegeneator.appspot.com",
  messagingSenderId: "340327402365",
  appId: "1:340327402365:web:6af1f7b629cdca10971a14",
  measurementId: "G-6VK0L3PNQD"
};

// Initialize Firebase
const app = initializeApp(firebaseConfig);
const db = getFirestore(app);
const storage = getStorage(app);
const analytics = getAnalytics(app);
const auth = getAuth(app); // Add this line to get the auth instance

// Function to send a password reset email
const sendPasswordReset = (email) => {
  sendPasswordResetEmail(auth, email)
    .then(() => {
      console.log("Password reset email sent successfully!");
      // Handle success (e.g., display a success message to the user)
    })
    .catch((error) => {
      console.error("Error sending password reset email:", error);
      // Handle error (e.g., display an error message to the user)
    });
};

export {
  getAuth,
  signInWithPopup,
  GoogleAuthProvider,
  db,
  storage,
  signInWithEmailAndPassword,
  analytics,
  logEvent,
  createUserWithEmailAndPassword,
  sendPasswordReset // Export the function for use in your Forgot Password page
};