import React, { useState, useEffect } from "react";
import { Paper, Grid } from "@mui/material";
import { signInWithEmailAndPassword, getAuth } from "../../functions/firebase";
import { signInUsingGoogle,getData,saveData } from "../../functions/functions";
import "./LoginPage.css";
// import Header from "../components/Header";

export default function LoginPage() {
  // declare firebase auth variable
  const auth = getAuth();
  const loginUser = () => {
    signInWithEmailAndPassword(auth, email, password).then((res) => {
      alert("User Logged In")

      getData('Users', res.user.uid).then((doc) => {
        if (doc.data()) {
          const user = doc.data();
          const {subscribed} = user
          localStorage.setItem("uid", res.user.uid);
          localStorage.setItem("subscribed", subscribed);
          window.location.href = "/";
        } else {
          saveData('Users', res.user.uid, {
            id: res.user.uid,
            email: "",
            userName: "",
          }).then(() => {
            localStorage.setItem("uid", res.user.uid);
            localStorage.setItem("subscribed", false);
            window.location.href = "/";
          });
        }
      });
    });
  };     
  // declare variable to save email
  const [email, setEmail] = useState("");
  // declare variable to save password
  const [password, setPassword] = useState("");
  return (
    <div  style={{ backgroundImage: 'url("")', backgroundSize: "full" }}>
      {/* <Header></Header> */}
      <div id="recaptcha-container"></div>
      <Grid
        container
        style={{ height: "100vh" }}
        alignItems={"center"}
        justifyContent={"center"}
      >
        <Paper elevation={6} style={{ padding: 40, width: 400 }}>
          <Grid container direction={"column"} alignItems={"center"}>
            <h2>Login</h2>
            <input
              placeholder="Email"
              className="form-control form-control-sm"
              value={email}
              onChange={(e) => {
                setEmail(e.target.value);
              }}
            ></input>
            <input
              placeholder="Password"
              className="form-control form-control-sm"
              type="password"
              value={password}
              style={{marginTop:20, marginBottom:20}}
              onChange={(e) => {
                setPassword(e.target.value);
              }}
            ></input>
            <button
              id="logout"
              type="button"
              style={{width:"100%", marginBottom:20}}
              className="btn btn-primary btn-block"
              onClick={() => {
                loginUser();
              }}
            >
              Login
            </button>
            <button
              id="logout"
              type="button"
              style={{width:"100%",marginBottom:20}}
              className="btn btn-primary btn-block"
              onClick={() => {
                window.location.href="/register"
              }}
            >
              Register
            </button>       
            <button
              id="logout"
              type="button"
              style={{width:"100%",marginBottom:20}}
              className="btn btn-primary btn-block"
              onClick={() => {
                signInUsingGoogle();
              }}
            >
                Sign in Using Google              
                <img src="google-logo.png" alt="" className="googlelogo"style={{ width: 15, height: 15 }}  />


            </button>
            <button
              id="logout"
              type="button"
              style={{width:"100%"}}
              className="btn btn-primary btn-block"
              onClick={() => {
                signInUsingGoogle();
              }}
            >
                <img src="apple-logo.png" alt="" className="applelogo" style={{ width: 12, height: 12 }}  />
              Sign in Using Apple

            </button>            
            <a href="/passwordreset" style={{ marginTop: 10 }}>
            Forgot Password?
            </a>
          </Grid>
        </Paper>
      </Grid>
    </div>
  );
}
